import React, {Component} from "react";
import {
    BrowserRouter,
    HashRouter,
    Switch,
    Redirect,
    Route,
    Link,
    useHistory,
} from "react-router-dom";
import MainPage from "./components/MainPage";
import ArticlePage from "./components/ArticlePage";
import ManagementPage from "./components/ManagementPage";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import connect from "react-redux/es/connect/connect";
import {loginUser, logoutUser} from "./actions/authentication";
import {setArticleFilter} from "./actions/article";
import Container from "react-bootstrap/Container";
import CreateArticlePage from "./components/CreateArticlePage";
import Card from "react-bootstrap/Card";
import {Row,Col} from "react-bootstrap";
import Collapse from "react-bootstrap/Collapse";


class Routes extends Component{

    constructor(){
        super();
        this.state = ({
            search: false,
        });

        this.searchRef = React.createRef();

        this.onlogin.bind(this);
        this.onlogout.bind(this);
    }

    onlogin(usr,pw){
        console.log("login...");
        // TEST
        let user={
            name:usr,
            password:pw
        }
        this.props.loginUser(user)
    }

    onlogout(){
        console.log("logout...");
        this.props.logoutUser();
        console.log("logout success:"+this.props.isAuthenticated);
    }

    handleSubmit(event){
        event.preventDefault();
        event.stopPropagation();
        if(this.props.isAuthenticated){
            this.onlogout();
        }else{
            this.onlogin(event.currentTarget.elements[0].value,event.currentTarget.elements[1].value)
        }
    }

    renderAbout(){
        window.scrollTo(0, 300);
        return(
            <Container className="fluid pt-5" style={{maxWidth:"50rem",border:"none"}}>
                <Card>
                    <Card.Header className="text-center" style={{fontSize:"1.5rem"}}>About Finland China News</Card.Header>
                    <Row>
                    <Col style={{maxWidth:"10%"}}>
                            <div className="bg-success" style={{height:"100%",  width:"1rem"}}/>
                    </Col>
                    <Col className="col-xs-16 col-sm-16 col-md-16">
                        <Card.Body>
                            <Card.Text style={{fontFamily:'Verdana'}}>
                                <p>
                                   Finland China News was established with the goal to build and expand the Finland China business community. The aim for this platform is to help companies and people make better decisions through access to highly relevant information.
                                </p>
                                <p>
                                    Today, there are around 400 Finnish companies in China, and around 100 Chinese companies in Finland. Our vision is to become the leading knowledge platform for accessing information that affect the companies, investors and people in this market.
                                </p>
                            </Card.Text>
                        </Card.Body>
                    </Col>
                    </Row>
                </Card>
            </Container>
        );
    }

    renderContact(){
        return(
            <Container style={{padding:"5rem"}}>
                <Card>
                    <Card.Header className="text-center" style={{fontSize:"1.5rem"}}>Contact Information</Card.Header>
                    <Row>
                    <Col className="col-sm-1">
                            <div className="bg-success" style={{height:"100%",width:"1rem"}}></div>
                    </Col>
                        <Col>
                        <Card.Body>
                            <Card.Text>

                            </Card.Text>
                        </Card.Body>
                    </Col>
                    </Row>
                </Card>
            </Container>
        );
    }

    render() {
        return (

            <HashRouter basename="/">
                <div className="logo" style={{textAlign:"center", backgroundPosition:"center", height:"20rem", backgroundImage:`url(https://defaultwebresources.blob.core.windows.net/resources/forest_lake.jpg)`}}>
                    <h1 style={{paddingTop:"5rem", textAlign: "center", fontFamily: "Garamond", color:"white"}}>Finland China News<p>芬 中 新 闻​</p></h1>
                </div>
                <Navbar onClick={() => {
                    if(this.state.search){
                        this.setState({
                            search:false,
                        });
                    }
                    this.props.setArticleFilter("");
                    this.searchRef.value = "";
                }} collapseOnSelect className="bg-light mb-2" expand="lg">
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="justify-content-center" style={{ width: "100%",fontSize:"1.8rem"}}>
                            <Nav.Link eventKey="1" className="mr-5 ml-5 text-center">
                                <Link className="nav-link" to={"/mainpage"}>Home</Link>
                            </Nav.Link>
                            <Nav.Link eventKey="2" className="mr-5 ml-5 text-center">
                                <Link className="nav-link" to={"/about"}>About</Link>
                            </Nav.Link>
                            <Nav.Link eventKey="3" className="mr-5 ml-5 text-center">
                                <Link className="nav-link" to={"/contact"}>Contact</Link>
                            </Nav.Link>
                            <Nav.Link className="mr-5 ml-5 text-center">
                                <Nav.Link eventKey="4" onClick={() => this.setState({search:!this.state.search})}>Search</Nav.Link>
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

                <Collapse in={this.state.search}>
                    <Container>
                        <Form.Control ref={ref => this.searchRef = ref} className="text-center" placeholder="search for article" onChange={(evt) => this.props.setArticleFilter(evt.target.value.toLowerCase())}/>
                    </Container>
                </Collapse>

                {this.props.filter !== "" && window.location.hash === 'mainpage' &&
                    <Container>
                        <Row>
                            <Button
                                onClick={() => {
                                    this.props.setArticleFilter("");
                                    this.setState({search:false});
                                    this.searchRef.value = "";
                                }}
                                style={{fontWeight:'bold', border:'none', color:'gray', background:'transparent'}}
                            >&#10060;</Button>
                            <h3>Showing articles with keyword '{this.props.filter}':</h3>
                        </Row>
                    </Container>
                }

                <Switch>
                    <Route exact path="/">
                        <Redirect to="/mainpage" />
                    </Route>
                    <Route path="/mainpage">
                        <MainPage/>
                    </Route>
                    <Route path="/article">
                        <ArticlePage/>
                    </Route>
                    <Route path="/about">
                        {this.renderAbout()}
                    </Route>
                    <Route path="/contact">
                        {this.renderContact()}
                    </Route>
                    {this.props.isAuthenticated &&
                    <Route path="/management">
                        <ManagementPage/>
                    </Route>
                    }
                    {this.props.isAuthenticated &&
                    <Route path="/create">
                        <CreateArticlePage/>
                    </Route>
                    }
                </Switch>

                <div style={{width:"100%", height:"8rem", textAlign:"center"}}>
                    <p style={{marginTop:"5rem"}}>© Finland China News</p><p>2020</p>
                </div>
                    <Container style={{position:"relative", marginBottom:"2rem"}}>
                    <Form onSubmit={this.handleSubmit.bind(this)} className="align-text-bottom">
                        {!this.props.isAuthenticated &&
                            <Row className="justify-content-center row-cols-sm-3" style={{margin:"auto",float:"none"}}>
                                <Col className="col-sm-2">
                                    <Form.Control required style={{paddingRight:"0", marginRight:"0"}} size="sm" placeholder="username"/>
                                </Col>
                                <Col className="col-sm-2">
                                    <Form.Control required size="sm" type="password" placeholder="password"/>
                                </Col>
                                <Col className="col-sm-1">
                                    <Button className="btn-secondary" size="sm" type="submit" >Login</Button>
                                    <a href="#" style={{color:"red", fontSize:"0.7rem"}}>
                                        {this.props.errorMessage}
                                    </a>
                                </Col>
                            </Row>
                        }

                        {this.props.isAuthenticated &&
                        <Row className="justify-content-center row-cols-sm-2" style={{margin:"auto",float:"none",width:"60%"}}>
                            <Col className="col-sm-2">
                                <Route render={({history}) => (
                                <Button className="btn-secondary" size="sm" onClick={() => history.push('/')} type="submit" >Logout</Button>
                                    )}/>
                            </Col>
                            <Col className="col-sm-2">
                                <Route render={({history}) => (
                                    <Button className="btn-secondary" size="sm" onClick={() => history.push('/management')} >Manage</Button>
                                )}/>
                            </Col>
                        </Row>
                        }
                    </Form>
                    </Container>
            </HashRouter>

        );
    }
}

function mapStateToProps(state) {
    const articles = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        article_info: articles.article_info,
        filter: articles.filter,
        isAuthenticated: auth.isAuthenticated,
        errorMessage: auth.errorMessage,
    }
}

export default connect(mapStateToProps, {
    loginUser,
    logoutUser,
    setArticleFilter,
})(Routes);

export function useRouter(){
    const history = useHistory();
    return {
        history,
    }
}